function useInView(threshold = 0.1) {
  const ref = useRef(null);
  const [inView, setInView] = useState(false);
  useEffect(() => {
    const obs = new IntersectionObserver(([e]) => { if (e.isIntersecting) setInView(true); }, { threshold });
    if (ref.current) obs.observe(ref.current);
    return () => obs.disconnect();
  }, []);
  return [ref, inView];
}

function useWindowWidth() {
  const [w, setW] = useState(typeof window !== "undefined" ? window.innerWidth : 1200);
  useEffect(() => {
    const handler = () => setW(window.innerWidth);
    window.addEventListener("resize", handler);
    return () => window.removeEventListener("resize", handler);
  }, []);
  return w;
}

function FadeUp({ children, delay = 0, style = {} }) {
  const [ref, inView] = useInView();
  return (
    <div ref={ref} style={{ opacity: inView ? 1 : 0, transform: inView ? "translateY(0)" : "translateY(36px)", transition: `opacity 0.75s ease ${delay}ms, transform 0.75s ease ${delay}ms`, ...style }}>
      {children}
    </div>
  );
}

function SkillBar({ name, level, color, delay = 0 }) {
  const [ref, inView] = useInView();
  return (
    <div ref={ref} style={{ marginBottom: 20 }}>
      <div style={{ display: "flex", justifyContent: "space-between", marginBottom: 8, fontSize: 13 }}>
        <span style={{ color: "#ccc", fontWeight: 500 }}>{name}</span>
        <span style={{ color, fontWeight: 700 }}><CountUp end={level} suffix="%" duration={1400} /></span>
      </div>
      <div style={{ background: "#0f0f1a", borderRadius: 99, height: 5, overflow: "hidden" }}>
        <div style={{ height: "100%", width: inView ? level + "%" : "0%", background: `linear-gradient(90deg,${color},${color}88)`, borderRadius: 99, transition: `width 1.4s cubic-bezier(.4,0,.2,1) ${delay}ms`, boxShadow: `0 0 12px ${color}88`, position: "relative" }}>
          <div style={{ position: "absolute", right: 0, top: "50%", transform: "translateY(-50%)", width: 8, height: 8, borderRadius: "50%", background: color, boxShadow: `0 0 8px ${color}` }} />
        </div>
      </div>
    </div>
  );
}

function CountUp({ end, suffix = "", duration = 2000 }) {
  const [count, setCount] = useState(0);
  const [ref, inView] = useInView(0.3);
  const started = useRef(false);
  useEffect(() => {
    if (!inView || started.current) return;
    started.current = true;
    const target = parseFloat(end);
    if (isNaN(target)) { setCount(end); return; }
    const startTime = performance.now();
    const tick = (now) => {
      const p = Math.min((now - startTime) / duration, 1);
      const ease = 1 - Math.pow(1 - p, 3);
      setCount(Math.floor(ease * target));
      if (p < 1) requestAnimationFrame(tick);
      else setCount(target);
    };
    requestAnimationFrame(tick);
  }, [inView]);
  return <span ref={ref}>{count}{suffix}</span>;
}

function HoverWord({ word, color, pill = false }) {
  const [hovered, setHovered] = useState(false);
  if (pill) return (
    <span onMouseEnter={() => setHovered(true)} onMouseLeave={() => setHovered(false)} style={{ display: "inline-block", padding: "6px 14px", borderRadius: 99, fontSize: 12, fontWeight: 600, letterSpacing: 0.4, border: `1px solid ${hovered ? color + "88" : color + "30"}`, color: hovered ? color : color + "bb", background: hovered ? color + "18" : color + "08", transform: hovered ? "translateY(-3px) scale(1.06)" : "none", boxShadow: hovered ? `0 6px 20px ${color}30` : "none", transition: "all 0.22s cubic-bezier(.4,0,.2,1)", cursor: "default" }}>{word}</span>
  );
  return (
    <span onMouseEnter={() => setHovered(true)} onMouseLeave={() => setHovered(false)} style={{ display: "inline-block", position: "relative", fontWeight: 600, color: hovered ? color : "#bbb", transform: hovered ? "translateY(-2px) scale(1.06)" : "none", transition: "all 0.22s", cursor: "default", textShadow: hovered ? `0 0 20px ${color}99` : "none", padding: "0 2px" }}>
      {word}
      {hovered && <span style={{ position: "absolute", left: 0, right: 0, bottom: -2, height: 2, background: `linear-gradient(90deg,transparent,${color},transparent)`, borderRadius: 99, animation: "shimmer 1s linear infinite", backgroundSize: "200% auto" }} />}
    </span>
  );
}

