function SpaceBackground() {
  const mountRef = useRef(null);

  useEffect(() => {
    const container = mountRef.current;
    if (!container) return;

    const canvas = document.createElement("canvas");
    canvas.style.cssText = "position:absolute;inset:0;width:100%;height:100%;";
    container.appendChild(canvas);
    const ctx = canvas.getContext("2d");

    let W, H, raf, time = 0;

    // Warp stars  flying toward viewer
    let warpStars = [];
    // Orbital planets
    const TECH_PLANETS = [
      { name:"Python",    color:"#3b82f6", size:14, orbitX:0.18, orbitY:0.22, speed:0.0004, angle:0.0,  },
      { name:"React",     color:"#00f5d4", size:11, orbitX:0.82, orbitY:0.18, speed:0.0006, angle:1.2,  },
      { name:"ML",        color:"#ff2d78", size:16, orbitX:0.15, orbitY:0.75, speed:0.0003, angle:2.5,  },
      { name:"Flask",     color:"#f7b731", size:9,  orbitX:0.88, orbitY:0.72, speed:0.0005, angle:3.8,  },
      { name:"SQL",       color:"#a855f7", size:10, orbitX:0.5,  orbitY:0.12, speed:0.00035,angle:0.8,  },
      { name:"Spark",     color:"#ff6b35", size:13, orbitX:0.5,  orbitY:0.88, speed:0.00025,angle:4.1,  },
      { name:"NLP",       color:"#06b6d4", size:10, orbitX:0.25, orbitY:0.48, speed:0.00045,angle:1.9,  },
      { name:"Snowflake", color:"#60a5fa", size:11, orbitX:0.75, orbitY:0.45, speed:0.0002, angle:3.3,  },
      { name:"Azure",     color:"#0ea5e9", size:9,  orbitX:0.38, orbitY:0.85, speed:0.00038,angle:5.0,  },
      { name:"Power BI",  color:"#f59e0b", size:9,  orbitX:0.65, orbitY:0.82, speed:0.00028,angle:2.2,  },
      { name:"MATLAB",    color:"#c084fc", size:8,  orbitX:0.12, orbitY:0.55, speed:0.00032,angle:0.5,  },
      { name:"R",         color:"#818cf8", size:7,  orbitX:0.9,  orbitY:0.5,  speed:0.00022,angle:4.6,  },
    ];

    const rnd = (a,b) => a + Math.random()*(b-a);

    const setup = () => {
      W = canvas.width = container.offsetWidth || window.innerWidth;
      H = canvas.height = container.offsetHeight || 600;

      // Warp stars  each has a starting position and speed
      warpStars = Array.from({length: 280}, () => ({
        x: rnd(0, W), y: rnd(0, H),
        z: rnd(0, W),           // depth  0=near, W=far
        pz: 0,                   // previous z for trail
        size: rnd(0.5, 2.2),
        color: Math.random() > 0.8 ? (Math.random()>0.5 ? "#b0c8ff" : "#ffd8a8") : "#ffffff",
        speed: rnd(1.5, 5),
      }));
      // reset pz
      warpStars.forEach(s => { s.pz = s.z; });
    };

    setup();
    window.addEventListener("resize", setup);

    const draw = () => {
      time++;
      ctx.clearRect(0,0,W,H);

      // Deep space bg
      ctx.fillStyle = "#050510";
      ctx.fillRect(0,0,W,H);

      // Nebula clouds
      [
        {x:0.2,y:0.3,r:200,c:"0,245,212",a:0.025},
        {x:0.8,y:0.7,r:180,c:"168,85,247",a:0.03},
        {x:0.5,y:0.5,r:250,c:"255,45,120",a:0.015},
        {x:0.1,y:0.8,r:150,c:"247,183,49",a:0.02},
        {x:0.9,y:0.2,r:160,c:"59,130,246",a:0.025},
      ].forEach(n => {
        const ng = ctx.createRadialGradient(n.x*W, n.y*H, 0, n.x*W, n.y*H, n.r);
        ng.addColorStop(0, `rgba(${n.c},${n.a})`);
        ng.addColorStop(0.5, `rgba(${n.c},${n.a*0.4})`);
        ng.addColorStop(1, "rgba(0,0,0,0)");
        ctx.beginPath(); ctx.arc(n.x*W,n.y*H,n.r,0,Math.PI*2);
        ctx.fillStyle=ng; ctx.fill();
      });

      //  WARP STARS (flying toward viewer) 
      const cx2 = W/2, cy2 = H/2;
      warpStars.forEach(s => {
        s.pz = s.z;
        s.z -= s.speed * (1 + time * 0.0003); // accelerate slightly over time
        if (s.z <= 0) {
          // Reset to far away at random position
          s.x = rnd(0, W);
          s.y = rnd(0, H);
          s.z = W;
          s.pz = s.z;
        }

        // Project 3D  2D from center
        const scale = W / s.z;
        const sx = (s.x - cx2) * scale + cx2;
        const sy = (s.y - cy2) * scale + cy2;
        const pScale = W / s.pz;
        const px2 = (s.x - cx2) * pScale + cx2;
        const py2 = (s.y - cy2) * pScale + cy2;

        const radius = Math.max(0.3, s.size * scale * 0.5);
        const brightness = Math.min(1, (1 - s.z/W) * 1.4);

        // Draw trail (streak)
        const trailLen = Math.max(1, (W - s.z) / W * 12);
        if (trailLen > 1) {
          const grad = ctx.createLinearGradient(px2, py2, sx, sy);
          grad.addColorStop(0, `rgba(255,255,255,0)`);
          grad.addColorStop(1, `rgba(255,255,255,${brightness * 0.7})`);
          ctx.beginPath();
          ctx.moveTo(px2, py2);
          ctx.lineTo(sx, sy);
          ctx.strokeStyle = grad;
          ctx.lineWidth = radius * 0.8;
          ctx.stroke();
        }

        // Draw star dot
        const g = ctx.createRadialGradient(sx,sy,0,sx,sy,radius*2.5);
        g.addColorStop(0, `rgba(255,255,255,${brightness})`);
        g.addColorStop(1, `rgba(255,255,255,0)`);
        ctx.beginPath(); ctx.arc(sx,sy,radius*2.5,0,Math.PI*2);
        ctx.fillStyle = g; ctx.fill();
        ctx.beginPath(); ctx.arc(sx,sy,radius,0,Math.PI*2);
        ctx.fillStyle = `rgba(255,255,255,${brightness})`; ctx.fill();
      });

      //  TECH PLANETS (floating around the edges) 
      TECH_PLANETS.forEach((p, i) => {
        p.angle += p.speed;
        // Float in an elliptical path around their home position
        const floatR = 22 + Math.sin(time*0.001+i)*8;
        const px3 = p.orbitX * W + Math.cos(p.angle * 3) * floatR;
        const py3 = p.orbitY * H + Math.sin(p.angle * 3) * floatR * 0.5;

        // Skip if out of bounds
        if (px3 < -40 || px3 > W+40 || py3 < -40 || py3 > H+40) return;

        const pulse = 0.8 + 0.2 * Math.sin(time * 0.02 + i);
        const pr = p.size * pulse;

        // Atmosphere
        const ag = ctx.createRadialGradient(px3,py3,0,px3,py3,pr*4);
        ag.addColorStop(0, p.color+"55"); ag.addColorStop(1,"transparent");
        ctx.beginPath(); ctx.arc(px3,py3,pr*4,0,Math.PI*2);
        ctx.fillStyle=ag; ctx.fill();

        // Body
        const sg = ctx.createRadialGradient(px3-pr*0.3,py3-pr*0.3,0,px3,py3,pr);
        sg.addColorStop(0,"#fff"); sg.addColorStop(0.3,p.color+"ee");
        sg.addColorStop(1,p.color+"66");
        ctx.beginPath(); ctx.arc(px3,py3,pr,0,Math.PI*2);
        ctx.fillStyle=sg; ctx.fill();

        // Shadow
        const shd = ctx.createRadialGradient(px3+pr*0.35,py3+pr*0.35,0,px3,py3,pr);
        shd.addColorStop(0,"rgba(0,0,0,0)"); shd.addColorStop(0.7,"rgba(0,0,0,0.35)");
        ctx.beginPath(); ctx.arc(px3,py3,pr,0,Math.PI*2);
        ctx.fillStyle=shd; ctx.fill();

        // Label
        const labelAlpha = 0.5 + 0.5*Math.sin(time*0.018+i);
        ctx.save();
        ctx.globalAlpha = labelAlpha * 0.85;
        const fs = Math.max(8, p.size - 1);
        ctx.font = `700 ${fs}px system-ui,sans-serif`;
        const tw = ctx.measureText(p.name).width;
        const lx=px3, ly=py3-pr-12;
        ctx.fillStyle="rgba(5,5,18,0.7)";
        ctx.beginPath();
        if(ctx.roundRect) ctx.roundRect(lx-tw/2-3,ly-fs/2-2,tw+6,fs+4,3);
        else ctx.rect(lx-tw/2-3,ly-fs/2-2,tw+6,fs+4);
        ctx.fill();
        ctx.shadowColor=p.color; ctx.shadowBlur=6;
        ctx.fillStyle="#fff"; ctx.textAlign="center"; ctx.textBaseline="middle";
        ctx.fillText(p.name,lx,ly);
        ctx.restore();
      });

      raf = requestAnimationFrame(draw);
    };

    draw();
    return () => {
      cancelAnimationFrame(raf);
      window.removeEventListener("resize", setup);
      if (canvas.parentNode) canvas.parentNode.removeChild(canvas);
    };
  }, []);

  return <div ref={mountRef} style={{ position:"absolute", inset:0, zIndex:0 }} />;
}

function LiquidGlassCard({ service: s, isMobile }) {
  const [hovered, setHovered] = useState(false);
  const [mousePos, setMousePos] = useState({ x: 0.5, y: 0.5 });
  const cardRef = useRef(null);

  const handleMouseMove = (e) => {
    const rect = cardRef.current.getBoundingClientRect();
    setMousePos({
      x: (e.clientX - rect.left) / rect.width,
      y: (e.clientY - rect.top) / rect.height,
    });
  };

  const tiltX = hovered ? (mousePos.y - 0.5) * -14 : 0;
  const tiltY = hovered ? (mousePos.x - 0.5) * 14 : 0;
  const speculX = mousePos.x * 100;
  const speculY = mousePos.y * 100;

  // Parse hex color to rgb
  const hexToRgb = (hex) => {
    const r = parseInt(hex.slice(1,3),16);
    const g = parseInt(hex.slice(3,5),16);
    const b = parseInt(hex.slice(5,7),16);
    return `${r},${g},${b}`;
  };
  const rgb = hexToRgb(s.color);

  return (
    <div
      ref={cardRef}
      onMouseEnter={() => setHovered(true)}
      onMouseLeave={() => { setHovered(false); setMousePos({ x: 0.5, y: 0.5 }); }}
      onMouseMove={handleMouseMove}
      style={{
        position: "relative", height: "100%",
        transform: hovered
          ? `perspective(800px) rotateX(${tiltX}deg) rotateY(${tiltY}deg) translateY(-8px) scale(1.02)`
          : "perspective(800px) rotateX(0deg) rotateY(0deg) translateY(0px) scale(1)",
        transition: hovered ? "transform 0.1s ease" : "transform 0.55s cubic-bezier(.2,.8,.2,1)",
        transformStyle: "preserve-3d",
        cursor: "default",
      }}
    >
      {/* Outer colour glow */}
      <div style={{
        position: "absolute", inset: -2, borderRadius: 24, pointerEvents: "none",
        background: `radial-gradient(circle at ${speculX}% ${speculY}%, ${s.color}30, transparent 65%)`,
        opacity: hovered ? 1 : 0,
        transition: "opacity 0.35s",
        filter: "blur(10px)",
      }} />

      {/* Liquid Glass card */}
      <div style={{
        position: "relative", height: "100%", borderRadius: 20, overflow: "hidden",
        background: hovered
          ? `linear-gradient(145deg, rgba(255,255,255,0.18) 0%, rgba(255,255,255,0.07) 40%, rgba(${rgb},0.12) 100%)`
          : `linear-gradient(145deg, rgba(255,255,255,0.1) 0%, rgba(255,255,255,0.04) 50%, rgba(255,255,255,0.07) 100%)`,
        backdropFilter: "blur(30px) saturate(200%) brightness(1.12)",
        WebkitBackdropFilter: "blur(30px) saturate(200%) brightness(1.12)",
        border: "1px solid rgba(255,255,255,0.18)",
        boxShadow: hovered
          ? `0 28px 70px rgba(0,0,0,0.55), inset 0 1px 0 rgba(255,255,255,0.38), inset 0 -1px 0 rgba(0,0,0,0.22), 0 0 0 0.5px rgba(255,255,255,0.22), 0 0 50px ${s.color}20`
          : `0 8px 32px rgba(0,0,0,0.4), inset 0 1px 0 rgba(255,255,255,0.22), inset 0 -1px 0 rgba(0,0,0,0.18), 0 0 0 0.5px rgba(255,255,255,0.1)`,
        transition: "box-shadow 0.4s cubic-bezier(.2,.8,.2,1), background 0.4s",
        padding: isMobile ? "22px 18px" : "28px 24px",
      }}>

        {/* iOS 26 specular highlight  follows cursor */}
        <div style={{
          position: "absolute", inset: 0, borderRadius: 20, pointerEvents: "none", zIndex: 1,
          background: `radial-gradient(ellipse 65% 45% at ${speculX}% ${speculY}%, rgba(255,255,255,${hovered ? 0.2 : 0.07}), transparent 68%)`,
          transition: hovered ? "none" : "all 0.5s",
        }} />

        {/* Top rim  bright edge like real glass */}
        <div style={{
          position: "absolute", top: 0, left: "8%", right: "8%", height: 1, zIndex: 2,
          background: `linear-gradient(90deg, transparent, rgba(255,255,255,${hovered ? 0.75 : 0.4}), transparent)`,
          transition: "all 0.3s",
          pointerEvents: "none",
        }} />

        {/* Colour refraction bar at top */}
        <div style={{
          position: "absolute", top: 0, left: 0, right: 0, height: 3, zIndex: 2,
          background: `linear-gradient(90deg, transparent 0%, ${s.color}bb 30%, ${s.color}ff 50%, ${s.color}88 70%, transparent 100%)`,
          filter: "blur(0.4px)",
          pointerEvents: "none",
        }} />

        {/* Left rim */}
        <div style={{
          position: "absolute", top: "8%", bottom: "8%", left: 0, width: 1, zIndex: 2,
          background: "linear-gradient(180deg, transparent, rgba(255,255,255,0.22), transparent)",
          pointerEvents: "none",
        }} />

        {/* Bottom reflection  glass reflects floor */}
        <div style={{
          position: "absolute", bottom: 0, left: 0, right: 0, height: "30%", zIndex: 2,
          background: "linear-gradient(to top, rgba(255,255,255,0.04), transparent)",
          pointerEvents: "none",
        }} />

        {/* Number */}
        <div style={{ position: "absolute", top: 14, right: 16, fontSize: 11, fontWeight: 900, color: "rgba(255,255,255,0.1)", letterSpacing: 2, zIndex: 3 }}>{s.number}</div>

        {/* Content */}
        <div style={{ position: "relative", zIndex: 3 }}>
          {/* Icon bubble */}
          <div style={{
            display: "inline-flex", alignItems: "center", justifyContent: "center",
            width: 52, height: 52, borderRadius: 16, marginBottom: 16,
            background: "linear-gradient(145deg, rgba(255,255,255,0.18), rgba(255,255,255,0.06))",
            backdropFilter: "blur(16px)",
            border: "1px solid rgba(255,255,255,0.22)",
            boxShadow: `inset 0 1px 0 rgba(255,255,255,0.35), 0 4px 20px rgba(0,0,0,0.3), 0 0 24px ${s.color}44`,
            fontSize: 24,
            transform: hovered ? "translateZ(20px) scale(1.1)" : "translateZ(0px) scale(1)",
            transition: "transform 0.4s cubic-bezier(.2,.8,.2,1)",
          }}>{s.icon}</div>

          <h3 style={{ fontWeight: 800, fontSize: isMobile ? 15 : 16, marginBottom: 8, color: "rgba(255,255,255,0.95)", letterSpacing: -0.3, textShadow: "0 1px 6px rgba(0,0,0,0.6)" }}>{s.title}</h3>
          <p style={{ color: "rgba(255,255,255,0.42)", fontSize: 13, lineHeight: 1.75, marginBottom: 18 }}>{s.desc}</p>

          {/* Capability pills  glass morphism style */}
          <div style={{ display: "flex", flexDirection: "column", gap: 6 }}>
            {s.items.map(item => (
              <div key={item} style={{
                display: "flex", alignItems: "center", gap: 8,
                padding: "7px 12px", borderRadius: 10,
                background: "rgba(255,255,255,0.06)",
                border: "1px solid rgba(255,255,255,0.1)",
                backdropFilter: "blur(8px)",
              }}>
                <div style={{ width: 5, height: 5, borderRadius: "50%", background: s.color, boxShadow: `0 0 8px ${s.color}cc`, flexShrink: 0 }} />
                <span style={{ fontSize: 12, color: "rgba(255,255,255,0.5)" }}>{item}</span>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}


