//  SHOOTING STARS 
function ShootingStars() {
  const canvasRef = useRef(null);
  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;
    const ctx = canvas.getContext("2d");
    let animId;

    const resize = () => { canvas.width = canvas.offsetWidth; canvas.height = canvas.offsetHeight; };
    resize();
    window.addEventListener("resize", resize);

    const shoots = [];
    const spawnShoot = () => {
      const angle = (Math.random() * 30 + 15) * Math.PI / 180;
      const startX = Math.random() * canvas.width * 0.7;
      const startY = Math.random() * canvas.height * 0.4;
      shoots.push({
        x: startX, y: startY,
        vx: Math.cos(angle) * (6 + Math.random() * 6),
        vy: Math.sin(angle) * (6 + Math.random() * 6),
        len: 80 + Math.random() * 120,
        alpha: 1, life: 1,
        color: ["rgba(255,255,255,", "rgba(180,200,255,", "rgba(0,245,212,"][Math.floor(Math.random() * 3)],
      });
    };

    let spawnTimer = 0;
    const draw = () => {
      canvas.width = canvas.width; // clear
      spawnTimer++;
      if (spawnTimer > 90 + Math.random() * 120) { spawnShoot(); spawnTimer = 0; }

      shoots.forEach((s, i) => {
        s.x += s.vx; s.y += s.vy; s.life -= 0.018;
        if (s.life <= 0) { shoots.splice(i, 1); return; }
        const tail = ctx.createLinearGradient(s.x, s.y, s.x - s.vx*(s.len/s.vx||8), s.y - s.vy*(s.len/s.vy||8));
        tail.addColorStop(0, s.color + Math.min(s.life, 0.9).toFixed(2) + ")");
        tail.addColorStop(1, s.color + "0)");
        ctx.beginPath();
        ctx.strokeStyle = tail;
        ctx.lineWidth = 1.5;
        ctx.moveTo(s.x, s.y);
        const tailLen = s.len;
        const nx = s.vx / Math.sqrt(s.vx*s.vx + s.vy*s.vy);
        const ny = s.vy / Math.sqrt(s.vx*s.vx + s.vy*s.vy);
        ctx.lineTo(s.x - nx*tailLen, s.y - ny*tailLen);
        ctx.stroke();
        // head glow
        ctx.beginPath();
        ctx.arc(s.x, s.y, 2, 0, Math.PI*2);
        ctx.fillStyle = s.color + Math.min(s.life, 1).toFixed(2) + ")";
        ctx.fill();
      });

      animId = requestAnimationFrame(draw);
    };
    draw();
    return () => { cancelAnimationFrame(animId); window.removeEventListener("resize", resize); };
  }, []);

  return (
    <canvas ref={canvasRef} style={{ position:"absolute", inset:0, width:"100%", height:"100%", zIndex:2, pointerEvents:"none" }} />
  );
}



