function FloatingTags({ isMobile }) {
  return (
    <div style={{
      position: "absolute",
      right: 0, top: 0, bottom: 0,
      width: "0",
      pointerEvents: "none", zIndex: 3,
      overflow: "hidden",
    }}>
      <style>{`
        @keyframes tagPopFloat {
          0%   { opacity:0; transform:translate(-50%,-50%) scale(0.4) translateY(20px); }
          15%  { opacity:1; transform:translate(-50%,-50%) scale(1.08) translateY(-4px); }
          25%  { transform:translate(-50%,-50%) scale(1) translateY(0px); }
          60%  { transform:translate(-50%,-50%) scale(1) translateY(-14px); }
          100% { transform:translate(-50%,-50%) scale(1) translateY(0px); }
        }
        @keyframes tagFloat0 { 0%,100%{transform:translate(-50%,-50%) translateY(0)}    50%{transform:translate(-50%,-50%) translateY(-14px)} }
        @keyframes tagFloat1 { 0%,100%{transform:translate(-50%,-50%) translateY(0)}    50%{transform:translate(-50%,-50%) translateY(-10px)} }
        @keyframes tagFloat2 { 0%,100%{transform:translate(-50%,-50%) translateY(0)}    50%{transform:translate(-50%,-50%) translateY(-18px)} }
        @keyframes tagFloat3 { 0%,100%{transform:translate(-50%,-50%) translateY(0)}    50%{transform:translate(-50%,-50%) translateY(-8px)}  }
        @keyframes tagFloat4 { 0%,100%{transform:translate(-50%,-50%) translateY(0)}    50%{transform:translate(-50%,-50%) translateY(-16px)} }
        @keyframes tagFloat5 { 0%,100%{transform:translate(-50%,-50%) translateY(0)}    50%{transform:translate(-50%,-50%) translateY(-12px)} }
      `}</style>

      {TECH_TAGS.map((tag, i) => (
        <div key={tag.label} style={{
          position: "absolute",
          left: isMobile ? `${20 + (i % 4) * 22}%` : `${tag.x}%`,
          top:  isMobile ? `${10 + Math.floor(i / 4) * 18}%` : `${tag.y}%`,
          opacity: 0,
          animation: [
            `tagPopFloat 1s cubic-bezier(0.34,1.56,0.64,1) ${tag.fd}s forwards`,
            `tagFloat${i % 6} ${tag.dur}s ease-in-out ${tag.fd + 1}s infinite`,
          ].join(", "),
        }}>
          <div style={{
            display: "inline-flex", alignItems: "center", gap: 6,
            padding: isMobile ? "4px 9px" : "5px 13px",
            borderRadius: 99,
            background: `linear-gradient(135deg,${tag.col}14,${tag.col}06)`,
            border: `1px solid ${tag.col}35`,
            backdropFilter: "blur(10px)",
            WebkitBackdropFilter: "blur(10px)",
            boxShadow: `0 0 14px ${tag.col}18, inset 0 1px 0 rgba(255,255,255,0.06)`,
            whiteSpace: "nowrap",
          }}>
            <div style={{
              width: 5, height: 5, borderRadius: "50%",
              background: tag.col, boxShadow: `0 0 8px ${tag.col}`, flexShrink: 0,
            }} />
            <span style={{
              fontSize: isMobile ? 9 : 11, fontWeight: 600, letterSpacing: 0.3,
              color: tag.col, fontFamily: "system-ui,sans-serif",
            }}>{tag.label}</span>
          </div>
        </div>
      ))}
    </div>
  );
}


