function PortfolioApp() {
  return <><Portfolio /><AIChatbot />
      <ScrollButtons />
      <ScrollProgressBar />
    </>;
}

//  SCROLL BUTTON 
function ScrollButtons() {
  const [atBottom, setAtBottom] = useState(false);
  const [visible, setVisible]   = useState(true);

  useEffect(() => {
    const onScroll = () => {
      const scrolled = window.scrollY;
      const total    = document.body.scrollHeight - window.innerHeight;
      if (total <= 0) return;
      const pct = scrolled / total;
      setAtBottom(pct > 0.85);
      // hide in middle range
      setVisible(pct < 0.12 || pct > 0.85);
    };
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  const handleClick = () => {
    if (atBottom) window.scrollTo({ top: 0, behavior: "smooth" });
    else          window.scrollTo({ top: document.body.scrollHeight, behavior: "smooth" });
  };

  return (
    <button
      onClick={handleClick}
      title={atBottom ? "Scroll to top" : "Scroll to bottom"}
      style={{
        position: "fixed",
        bottom: 100,
        right: 24,
        width: 46, height: 46,
        borderRadius: "50%",
        border: "none",
        cursor: "pointer",
        zIndex: 299,
        display: "flex", alignItems: "center", justifyContent: "center",
        background: atBottom
          ? "linear-gradient(135deg,rgba(0,245,212,0.18),rgba(0,245,212,0.08))"
          : "linear-gradient(135deg,rgba(168,85,247,0.18),rgba(168,85,247,0.08))",
        color: atBottom ? "#00f5d4" : "#a855f7",
        backdropFilter: "blur(14px)",
        WebkitBackdropFilter: "blur(14px)",
        border: atBottom ? "1px solid rgba(0,245,212,0.25)" : "1px solid rgba(168,85,247,0.25)",
        boxShadow: atBottom
          ? "0 4px 20px rgba(0,245,212,0.2), inset 0 1px 0 rgba(255,255,255,0.08)"
          : "0 4px 20px rgba(168,85,247,0.2), inset 0 1px 0 rgba(255,255,255,0.08)",
        opacity: visible ? 1 : 0,
        transform: visible ? "scale(1)" : "scale(0.7)",
        transition: "all 0.35s cubic-bezier(0.34,1.56,0.64,1)",
        pointerEvents: visible ? "auto" : "none",
      }}
    >
      <svg width="17" height="17" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2.5" strokeLinecap="round" strokeLinejoin="round">
        {atBottom
          ? <polyline points="18 15 12 9 6 15"/>
          : <polyline points="6 9 12 15 18 9"/>
        }
      </svg>
    </button>
  );
}

//  SCROLL PROGRESS BAR 
function ScrollProgressBar() {
  const [progress, setProgress] = useState(0);

  useEffect(() => {
    const onScroll = () => {
      const scrolled = window.scrollY;
      const total = document.body.scrollHeight - window.innerHeight;
      setProgress(total > 0 ? (scrolled / total) * 100 : 0);
    };
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  return (
    <div style={{
      position: "fixed", top: 0, left: 0, right: 0,
      height: 2, zIndex: 9999, background: "rgba(255,255,255,0.04)",
    }}>
      <div style={{
        height: "100%",
        width: `${progress}%`,
        background: "linear-gradient(90deg, #00f5d4, #a855f7, #ff2d78)",
        backgroundSize: "200% auto",
        animation: "shimmer 3s linear infinite",
        transition: "width 0.08s linear",
        boxShadow: "0 0 8px rgba(0,245,212,0.6), 0 0 16px rgba(168,85,247,0.3)",
      }} />
    </div>
  );
}


